@import '_variables';

*,
*:after,
*:before {
    box-sizing: border-box;
}

*:focus {
    outline: none;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.6rem;
}

button,
input,
optgroup,
select,
textarea {
    font-family: 'Open Sans', sans-serif;
}

.app {
    background: $bg-color1;
    display: flex;
}

.editor {
    height: 100vh;
    width: calc(100% - 320px);
}

.sidebar {
    background: $bg-color2;
    box-shadow: 2px 0 2px rgba(0, 0, 0, 0.125);
    height: 100vh;
    overflow: hidden;
    position: relative;
    width: 320px;

    &-list {
        list-style-type: none;
        height: calc(100vh - 153px);
        margin: 20px 0;
        overflow: scroll;
        padding: 0;

        &-item {
            background: $light-color;
            border: none;
            border-left: 0px solid $light-color;
            box-shadow: 0 3px 5px rgba(0, 0, 0, .125);
            cursor: default;
            height: 0;
            margin: 0 auto;
            opacity: 0;
            padding: 0 15px;
            position: relative;
            width: calc(100% - 20px);
            transform: scaleY(0);
            transform-origin: center top;
            transition: all .25s ease-out;

            &.is-active {
                border-left: 5px solid $info-color;
                box-shadow: 0 6px 10px rgba(0, 0, 0, .25);
            }

            &.is-visible {
                height: auto;
                margin-bottom: 10px;
                padding-bottom: 10px;
                opacity: .66;
                padding-top: 10px;
                transform: scaleY(1);

                &.is-active {
                    opacity: 1;
                }
            }

            &-title {
                cursor: default;
                display: block;
                font-size: 16px;
                line-height: 1.6;
            }

            &-date {
                color: $border-color1;
                cursor: default;
                display: block;
                font-size: 12px;
                font-style: italic;
            }

            &-delete {
                color: $danger-color;
                cursor: pointer;
                font-size: 24px;
                line-height: 24px;
                position: absolute;
                right: 15px;
                top: 50%;
                transform: translateY(-50%);
                transition: all .25s ease-out;

                &:hover {
                    color: darken($danger-color, 20%);
                }
            }
        }
    }

    & > button {
        background: $success-color;
        border: none;
        bottom: 0;
        box-shadow: 0 0 10px rgba(0, 0, 0, .125);
        color: $light-color;
        cursor: pointer;
        font-size: 20px;
        font-weight: 400;
        height: 64px;
        left: 0;
        line-height: 63px;
        position: absolute;
        right: 0;
        text-shadow: 1px 1px 1px rgba(0, 0, 0, .125);
        transition: all .25s ease-out;
        width: 100%;

        &:hover {
            background: darken($success-color, 20%);
        }
    }
}

.login {
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 360px;
    z-index: 1001;

    h2 {
        font-size: 24px;
        font-weight: 400;
        line-height: 1;
        margin: 0 0 20px 0;
        text-align: center;
    }

    &-overlay {
        background: rgba(255, 255, 255, .95);
        height: 100%;
        left: 0;
        opacity: 0;
        position: fixed;
        pointer-events: none;
        top: 0;
        width: 100%;
        z-index: 1000;

        &.is-visible {
            opacity: 1;
            pointer-events: auto;
        }
    }

    & > label {
        font-size: 14px;
        line-height: 2.4;
    }

    input {
        border: 1px solid $border-color2;
        margin: 0 0 10px 0;
        padding: 10px 15px;
        width: 100%;

        &[type="submit"] {
            background: $info-color;
            border: none;
            color: $light-color;
            margin: 20px 0 0 0;

            &:hover {
                background: darken($info-color, 20%);
            }
        }
    }

    &-error {
        background: $danger-color;
        color: $light-color;
        display: none;
        margin: 0 0 10px 0;
        padding: 10px 15px;

        &.is-visible {
            display: block;
        }
    }

    &-loader {
        background: rgba(255, 255, 255, .95);
        display: none;
        height: 100%;
        line-height: 100vh;
        position: absolute;
        text-align: center;
        width: 100%;
        z-index: 1002;

        &.is-visible {
            display: block;
        }
    }
}

.search {
    padding: 0;
    width: 100%;

    &-input {
        border: none;
        border-bottom: 1px solid $border-color2;
        box-shadow: 0 0 10px rgba(0, 0, 0, .125);
        font-size: 16px;
        padding: 15px 20px;
        width: 100%;
    }
}

.editor {
    & > input {
        border: none;
        border-bottom: 1px solid $border-color2;
        display: block;
        font-family: 'Roboto Mono', monospace;
        font-size: 32px;
        font-weight: 300;
        height: 56px;
        margin: 40px auto;
        width: 780px;
    }

    & > .MDEditor {
        & > .MDEditor_toolbar {
            display: none;
        }

        & > .MDEditor_editor {
            border: none;
            height: calc(100vh - 176px);
            margin: 0 auto;
            width: 780px;

            .CodeMirror {
                color: #555;
                font-size: 20px;
                font-family: 'Roboto Mono', monospace;
                font-weight: 300;
                height: calc(100vh - 176px);
                line-height: 1.6;
            }
        }
    }
}

.cm-s-default {
    .cm-header {
        color: #111;
        font-weight: 500;

        &.cm-header-1 {
            font-size: 42px;
        }

        &.cm-header-2 {
            font-size: 36px;
        }

        &.cm-header-3 {
            font-size: 30px;
        }

        &.cm-header-4 {
            font-size: 26px;
        }

        &.cm-header-5 {
            font-size: 23px;
        }

        &.cm-header-6 {
            font-size: 20px;
        }
    }

    .cm-em,
    .cm-strong {
        color: #333;
        font-weight: 500;
    }

    .cm-link {
        color: #55b;
    }

    .cm-url {
        color: #55b;
    }

    .cm-variable-2 {
        color: #333;
    }

    .cm-quote {
        color: #aaa;
    }

    .cm-comment {
        background: #f0f0f0;
        color: #aaa;
        display: inline-block;
        width: 100%;

        &.cm-variable-2 {
            width: auto;
        }
    }
}
